import React from "react";

import {
    Button,
    Card,
    Container,
    Row,
    Col,
    CardBody,
    Form,
    FormGroup,
    InputGroup,
    InputGroupAddon,
    InputGroupText, Input,
    Nav, NavItem, NavLink,
    UncontrolledDropdown,
    UncontrolledCollapse,
    DropdownItem,
    DropdownToggle,
    Navbar,
    NavbarBrand, DropdownMenu,
    ButtonGroup, TabContent, TabPane,
    Badge,
    CardTitle,
    ListGroup, ListGroupItem,
    Media,
    Progress,
    Table,
    UncontrolledTooltip,
} from "reactstrap";

import {Link, Switch} from 'react-router-dom';

// core components
import DemoNavbar from "../../Navbars/LogoNavbar.js";
import SimpleFooter from "../../Footers/SimpleFooter.js";

class Profile extends React.Component {

    state = {
        iconTabs: 1,
        plainTabs: 1
    };

    componentDidMount() {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
    }

    toggleNavs = (e, state, index) => {
        e.preventDefault();
        this.setState({
            [state]: index
        });
    };

    render() {
        return (
            <Card className="bg-secondary shadow-none border-0 mb-0 contentDec contentMinHeight">
                <CardBody>
                    <Row>
                        <Col md="6">
                            <FormGroup>
                                <Input
                                    placeholder="Email"
                                    type="email"
                                />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                        </Col>
                        <Col md="6">
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="()" type="text" />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                            <FormGroup>
                                <Input placeholder="" type="text" />
                            </FormGroup>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
        );
    }
}

export default Profile;



