import React from "react";

import {
    Button,
    Card,
    Container,
    Row,
    Col,
    CardBody,
    Form,
    FormGroup,
    InputGroup,
    InputGroupAddon,
    InputGroupText, Input,
    Nav, NavItem, NavLink,
    UncontrolledDropdown,
    UncontrolledCollapse,
    DropdownItem,
    DropdownToggle,
    Navbar,
    NavbarBrand, DropdownMenu,
    ButtonGroup, TabContent, TabPane,
    Badge,
    CardTitle,
    ListGroup, ListGroupItem,
} from "reactstrap";

import { Link, Route, Switch } from 'react-router-dom';
import Buy from './pages/Buy';
import Robots from './pages/Robots';
import Transactions from './pages/Transactions';
import Profile from './pages/Profile';
import Withdrawal from './pages/Withdrawal';

// core components
import DemoNavbar from "../Navbars/LogoNavbar.js";
import SimpleFooter from "../Footers/SimpleFooter.js";

class Dashboard extends React.Component {

    state = {
        iconTabs: 1,
        plainTabs: 1
    };

    componentDidMount() {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
        this.refs.main.scrollTop = 0;
    }

    toggleNavs = (e, state, index) => {
        e.preventDefault();
        this.setState({
            [state]: index
        });
    };

    render() {
        return (
            <React.Fragment>
                <main ref="main">
                    <section className="section section-shaped section-lg" style={{paddingTop: '0rem'}}>
                        <div className="shape shape-style-1 green-gradient-bg">
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                        </div>
                        <Container className="pt-lg-md pr-0 pl-0 pr-md-1 pl-md-1">
                            <Row className="justify-content-center">
                                {/*<Col sm={12} lg={2}>*/}
                                {/*    */}
                                {/*</Col>*/}
                                <Col sm={12}>
                                    <Navbar className="navbar-dark bg-default rounded" expand="lg">
                                        <Container>
                                            <NavbarBrand
                                                href="#pablo"
                                                onClick={e => e.preventDefault()}
                                                className="text-white"
                                            >
                                                Argon
                                            </NavbarBrand>
                                        </Container>
                                    </Navbar>
                                </Col>
                                <Col sm={12} lg={2} className="pr-lg-0">
                                    <Card className="bg-secondary shadow-none border-0 sidebarDec" style={{height: '100%'}}>
                                        <CardBody className="pt-sm-4 pr-lg-0">
                                            <ListGroup>
                                                <ListGroupItem>
                                                    <Link to="/profile">
                                                        
                                                    </Link>
                                                </ListGroupItem>
                                                <ListGroupItem>
                                                    <Link to="/buy">
                                                        
                                                    </Link>
                                                </ListGroupItem>
                                                <ListGroupItem>
                                                    <Link to="/robots">
                                                         
                                                    </Link>
                                                </ListGroupItem>
                                                <ListGroupItem>
                                                    <Link to="/trades">
                                                         
                                                    </Link>
                                                </ListGroupItem>
                                                <ListGroupItem>
                                                    <Link to="/transactions">
                                                        
                                                    </Link>
                                                </ListGroupItem>
                                                <ListGroupItem>
                                                    <Link to="/account">
                                                         
                                                    </Link>
                                                </ListGroupItem>
                                            </ListGroup>
                                        </CardBody>
                                    </Card>
                                </Col>
                                <Col sm={12} lg={10} className="pl-lg-0">
                                    <Switch>
                                      <Route path="/buy" component={Buy} />
                                      <Route path="/robots" component={Robots} />
                                      <Route path="/transactions" component={Transactions} />
                                      <Route path="/profile" component={Profile} />
                                      <Route path="/account" component={Withdrawal} />
                                    </Switch>
                                </Col>
                            </Row>
                        </Container>
                    </section>
                </main>
                <SimpleFooter />
            </React.Fragment>
        );
    }
}

export default Dashboard;



