import React from "react";

import {
    Button,
    Card,
    Container,
    Row,
    Col,
    CardBody,
    Form,
    FormGroup,
    InputGroup,
    InputGroupAddon,
    InputGroupText, Input,
    Nav, NavItem, NavLink,
    UncontrolledDropdown,
    UncontrolledCollapse,
    DropdownItem,
    DropdownToggle,
    Navbar,
    NavbarBrand, DropdownMenu,
    ButtonGroup,
    TabContent,
    TabPane,
} from "reactstrap";

import { Link } from 'react-router-dom';

// core components
import DemoNavbar from "../Navbars/LogoNavbar.js";
import SimpleFooter from "../Footers/SimpleFooter.js";

class Profile extends React.Component {

    state = {
        iconTabs: 1,
        plainTabs: 1
    };

    componentDidMount() {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
        this.refs.main.scrollTop = 0;
    }

    toggleNavs = (e, state, index) => {
        e.preventDefault();
        this.setState({
            [state]: index
        });
    };

    render() {
        return (
            <React.Fragment>
                <DemoNavbar />
                <main ref="main">
                    <section className="section section-shaped section-lg">
                        <div className="shape shape-style-1 green-gradient-bg">
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                            <span />
                        </div>
                        <Container className="pt-lg-md">
                            <Row className="justify-content-center">
                                {/*<Col sm={12} lg={2}>*/}
                                {/*    */}
                                {/*</Col>*/}
                                <Col sm={12} lg={12}>

                                    <Navbar className="navbar-dark bg-primary rounded" expand="lg">
                                        <Container>
                                            <NavbarBrand
                                                href="#pablo"
                                                onClick={e => e.preventDefault()}
                                                className="text-white"
                                            >
                                                Menu
                                            </NavbarBrand>
                                            <button className="navbar-toggler" id="nav-inner-primary">
                                                <span className="navbar-toggler-icon" />
                                            </button>
                                            <UncontrolledCollapse navbar toggler="#nav-inner-primary">
                                                <div className="navbar-collapse-header">
                                                    <Row>
                                                        <Col className="collapse-brand" xs="6">
                                                            <Link to="/">
                                                                <img
                                                                    alt="..."
                                                                    src={require("../../assets/img/brand/argon-react.png")}
                                                                />
                                                            </Link>
                                                        </Col>
                                                        <Col className="collapse-close" xs="6">
                                                            <button
                                                                className="navbar-toggler"
                                                                id="nav-inner-primary"
                                                                type="button"
                                                            >
                                                                <span />
                                                                <span />
                                                            </button>
                                                        </Col>
                                                    </Row>
                                                </div>
                                                <Nav className="ml-lg-auto" navbar>
                                                    <NavItem>
                                                        <NavLink
                                                            tag={Link}
                                                            to="/robots"
                                                        >
                                                            
                                                        </NavLink>
                                                    </NavItem>
                                                    <NavItem>
                                                        <NavLink
                                                            tag={Link}
                                                            to="/trade"
                                                        >
                                                            
                                                        </NavLink>
                                                    </NavItem>

                                                    <NavItem>
                                                        <NavLink
                                                            tag={Link}
                                                            to="/account"
                                                        >
                                                             0.00$
                                                        </NavLink>
                                                    </NavItem>
                                                    <UncontrolledDropdown nav inNavbar>
                                                        <DropdownToggle nav>zbr</DropdownToggle>
                                                        <DropdownMenu
                                                            aria-labelledby="nav-inner-primary_dropdown_1"
                                                            right
                                                        >
                                                            <DropdownItem
                                                                tag={Link}
                                                                to="/profile"
                                                            >
                                                                
                                                            </DropdownItem>
                                                            <DropdownItem
                                                                href="#pablo"
                                                                onClick={e => e.preventDefault()}
                                                            >
                                                                
                                                            </DropdownItem>
                                                            <DropdownItem divider />
                                                            <DropdownItem
                                                                href="#pablo"
                                                                onClick={e => e.preventDefault()}
                                                            >
                                                                Something else here
                                                            </DropdownItem>
                                                        </DropdownMenu>
                                                    </UncontrolledDropdown>
                                                </Nav>
                                            </UncontrolledCollapse>
                                        </Container>
                                    </Navbar>
                                    <Card className="bg-secondary shadow border-0">
                                        <CardBody>
                                        </CardBody>
                                    </Card>
                                </Col>
                            </Row>
                        </Container>
                    </section>
                </main>
                <SimpleFooter />
            </React.Fragment>
        );
    }
}

export default Profile;



