import React from "react";
import { Container, Row } from "reactstrap";

// core components
import DemoNavbar from "../Navbars/DemoNavbar.js";
import CardsFooter from "../Footers/CardsFooter.js";

// index page sections
import Hero from "../LandingSections/Hero.js";
import PamAccounts from "../LandingSections/PamAccounts.js";
import Carousel from "../LandingSections/Carousel.js";
import Login from "../LandingSections/Login.js";
import Download from "../LandingSections/Download.js";

class Index extends React.Component {
    componentDidMount() {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
        this.refs.main.scrollTop = 0;
    }
    render() {
        return (
            <React.Fragment>
                <DemoNavbar />
                <main ref="main">
                    <Hero />
                    <Carousel />
                    <Login />
                    <PamAccounts/>
                    <Download />
                </main>
                <CardsFooter />
            </React.Fragment>
        );
    }
}

export default Index;



